    console.log("ticketsE1Num:",res19);
    this.ticketsE1Num =this.$util.hexToDec(res19._hex);

      let res20 =await this.$contract.getEIDNodesNum(2);
      console.log("ticketsE2Num:",res20);
      this.ticketsE2Num =this.$util.hexToDec(res20._hex);
      let res21 =await this.$contract.getEIDNodesNum(3);
      console.log("ticketsE3Num:",res21);
      this.ticketsE3Num =this.$util.hexToDec(res21._hex);
      let res22 =await this.$contract.getEIDNodesNum(4);
      console.log("ticketsE4Num:",res22);
      this.ticketsE4Num =this.$util.hexToDec(res22._hex);
      let res23 =await this.$contract.getEIDNodesNum(5);
      console.log("ticketsE5Num:",res23);
      this.ticketsE5Num =this.$util.hexToDec(res23._hex);

      let res24 =await this.$contract.getEIDsNum();
      console.log("ticketsEIDsNum:",res24);
      this.ticketsESDNum =this.$util.hexToDec(res24._hex)
  },

  methods: {
    /* 主合约 */
    //获取主合约投资的燃烧比例
    async getJoinBurnRate() {
        this.joinBurnRate = await this.$contract.getJoinBurnRate();
        this.joinBurnRate = this.$util.hexToDec(this.joinBurnRate._hex);
        console.log("this.joinBurnRate",this.joinBurnRate);
    },
    async getFloatingStaticFlag() {
          let res18 = await this.$contract.isFloatingStaticRate();
          this.isFloatingStaticRate = res18;
          console.log('this.isFloatingStaticRate:', this.isFloatingStaticRate)
    },
    // 设置静态收益
    async masterClick1 () {
      let number = this.masterInput1;
      if (this.$util.myLt(number, '1') || this.$util.myGt(number, '1000')) {
        console.log('金额不符合要求');
        return
      }
      console.log('点击了设置静态收益', this.masterInput1);
      let res = await this.$contract.setStaticRate(number);
      console.log('点击了设置静态收益-返回值', res)
    },

    // 设置动态加速层数收益率
    async masterClick2 () {
      let number1 = this.masterInput2;
      let number2 = this.masterInput22;
      if (this.$util.myLt(number1, '0') || this.$util.myGt(number1, '49') || this.$util.myLt(number2, '1') || this.$util.myGt(number2, '1000')) {
        console.log('金额不符合要求');
        return
      }
      console.log('设置动态加速层数收益率', this.masterInput2, this.masterInput22);
      let res = await this.$contract.setDynamicRates(number1, number2);
      console.log('设置动态加速层数收益率-返回值', res)
    },

    // 激活保险池
    async masterClick3 () {
      let steps = this.masterInput10;
      let res = await this.$contract.activeInsurance(steps);
      console.log('激活保险池-返回值', res)
    },
    // 激活保险池
      async masterClick13 () {
          let index = this.masterInput13 - 1;
          let res = await this.$contract.insurance(index);
          this.insuranceBalance = this.$util.div10e6(res);
          console.log(index,'号保险池余额:', res)
      },

    // 划转永续池
    async masterClick4 () {
      let address = this.masterInput4;
      if (!address) {
        console.log('地址不符合要求');
        return
      }
      let res = await this.$contract.activeSustainable(address);
      console.log('划转永续池-返回值', res)
    },

    // 领取技术团队奖励
    async masterClick5 () {
      let address = this.masterInput5;
      if (!address) {
        console.log('地址不符合要求');
        return
      }
      let res = await this.$contract.takeFounder_master(address);
      console.log('领取技术团队奖励-返回值', res)
    },

    // 领取拓展运营费用
    async masterClick6 () {
      let address = this.masterInput6;
      if (!address) {
        console.log('地址不符合要求');
        return
      }
      let res = await this.$contract.takeOperation(address);
      console.log('领取拓展运营费用-返回值', res)
    },

    // 领取做市资金
    async masterClick7 () {
      let address = this.masterInput7;
      if (!address) {
        console.log('地址不符合要求');
        return
      }
      let res = await this.$contract.takeDex(address);
      console.log('领取做市资金-返回值', res)
    },

    // 开关浮动利率
    async masterClick8 () {
      let res = await this.$contract.setFloatingFlag();
      console.log('设置浮动利率-返回值', res);
      setTimeout(async () => {
          //查看是否开启了浮动利率
          this.getFloatingStaticFlag()
        }, 2000)

    },

    // 设置主合约的投资上限值
    async masterClick9 () {
      let value = this.masterInput9;
      //value = this.$util.mul10e18(value);
      value = value * 1;
      let res = await this.$contract.setMaxInvestAmount(value);
      console.log('设置主合约的投资上限值-返回值', res)
    },
    //设置主合约的投资燃烧ECO的比例
      async masterClick11 () {
          let value = this.masterInput11;
          value = value * 1;
          let res = await this.$contract.setJoinBurnRate(value);
          console.log('设置主合约的投资燃烧ECO的比例-返回值', res);
          setTimeout(async () => {
              //获取当前管理者数量
              let res11 = await this.$contract.getJoinBurnRate();
              this.joinBurnRate = this.$util.hexToDec(res11._hex);
              console.log('this.joinBurnRate:', this.joinBurnRate)
          }, 2000)
      },

      // 设置主合约每日最大入场资金
      async masterClick12 () {
          let value = this.masterInput12;
          value = value * 1;
          let res = await this.$contract.setMaxInvestAmountPerDay(value);
          console.log('主合约每日最大入场资金-返回值', res);
          setTimeout(async () => {
              this.maxInvestAmountPerDay = await this.$contract.getMaxInvestAmountPerDay();
              this.maxInvestAmountPerDay = this.$util.hexToDec(this.maxInvestAmountPerDay._hex);
              console.log("this.maxInvestAmountPerDay",this.maxInvestAmountPerDay);
          }, 2000)
      },

      //获取主合约每日最大入场资金
      async getMaxInvestAmountPerDay() {
          this.maxInvestAmountPerDay = await this.$contract.getMaxInvestAmountPerDay();
          this.maxInvestAmountPerDay = this.$util.hexToDec(this.maxInvestAmountPerDay._hex);
          console.log("this.maxInvestAmountPerDay",this.maxInvestAmountPerDay);
      },

      // 设置分配主合约第三方奖励池的三个地址
      async masterClick14 () {
          let address1 = this.masterInput14;
          let address2 = this.masterInput15;
          let address3 = this.masterInput16;
          let res = await this.$contract.takeThirdPool_master(address1,address2,address3);
          console.log('设置分配主合约第三方奖励池的三个地址-返回值', res);
          setTimeout(async () => {
              this.thirdPool = await this.$contract.getThirdPool();
              this.thirdPool = this.$util.hexToDec(this.thirdPool._hex);
              console.log("this.thirdPool",this.thirdPool);
          }, 2000)
      },
      //获取主合约每日最大入场资金
      async getThirdPool() {
          this.thirdPool = await this.$contract.getThirdPool();
          this.thirdPool = this.$util.hexToDec(this.thirdPool._hex);
          this.thirdPool = this.$util.div10e6(this.thirdPool);
          console.log("this.thirdPool",this.thirdPool);
      },

      //设置主合约的投资空投ECO的比例
      async masterClick17 () {
          let value = this.masterInput17;
          value = value * 1;
          let res = await this.$contract.setAirdropRate(value);
          console.log('设置主合约的投资空投ECO的比例-返回值', res);
          setTimeout(async () => {
              let res17 = await this.$contract.getAirdropRate();
              this.airdropRate = this.$util.hexToDec(res17._hex);
              console.log('this.airdropRate:', this.airdropRate)
          }, 2000)
      },

      //获取主合约投资空投ECO的比例
      async getAirdropRate() {
          let res18 = await this.$contract.getAirdropRate();
          this.airdropRate = this.$util.hexToDec(res18._hex);
          console.log('this.airdropRate:', this.airdropRate)
      },


    /* 门票合约 */
    // 设置EID矩阵释放率
    async ticketsClick1 () {
      let number = this.ticketsInput1;
      if (this.$util.myLt(number, '1') || this.$util.myGt(number, '1000')) {
        console.log('金额不符合要求');
        return
      }
      let res = await this.$contract.setReleaseRate(number);
      console.log('设置EID矩阵释放率-返回值', this.ticketsInput1, res)
    },

    // 设置流动性挖矿解锁率
    async ticketsClick2 () {
      let number = this.ticketsInput2;
      if (this.$util.myLt(number, '1') || this.$util.myGt(number, '1000')) {
        console.log('金额不符合要求');
        return
      }
      let res = await this.$contract.setMiningRate(number);
      console.log('设置流动性挖矿解锁率-返回值', this.ticketsInput2, res)
    },

    // 设置EID激活层数收益率
    async ticketsClick3 () {
      let number1 = this.ticketsInput3;
      let number2 = this.ticketsInput32;
      if (this.$util.myLt(number1, '1') || this.$util.myGt(number1, '30') || this.$util.myLt(number2, '1') || this.$util.myGt(number2, '1000')) {
        console.log('金额不符合要求');
        return
      }
      /*if (number1 < 1 || number1 > 30 || number2 < 1 || number2 > 1000) {
        console.log('金额不符合要求');
        return
      }*/
      let res = await this.$contract.setActiveEidFloorRates(number1, number2);
      console.log(
        '设置EID激活层数收益率-返回值',
        this.ticketsInput3,
        this.ticketsInput32,
        res
      )
    },

    // 领取技术团队USDT奖励20%
    // async ticketsClick4() {
    // 	let res = await this.$contract.takeFounder();
    // 	console.log('领取技术团队USDT奖励20%-返回值', res);
    // },

    // 划转USDT到主合约80%
    async ticketsClick5 () {
      let address = this.ticketsInput5;
      let res = await this.$contract.transferSalesVolume(address);
      console.log('划转USDT到主合约80%-返回值', this.ticketsInput5, res)
    },

    // 增发门票
    async ticketsClick6 () {
      let address = this.ticketsInput6;
      let number = this.ticketsInput62;
      number = this.$util.mul10e18(number);
      let res = await this.$contract.geniusMint(address, number);
      console.log(
        '增发门票-返回值',
        this.ticketsInput6,
        this.ticketsInput62,
        res
      )
    },

    // 设置门票合约最大ECO兑换量
    async ticketsClick7 () {
      let value = this.ticketsInput7;
      //value = this.$util.mul10e18(value);
      value = value * 1;
      let res = await this.$contract.setMaxExEcoOnce(value);
      console.log('单次共振ECO的上限值-返回值', res)
    },
      // 设置门票合约每日最大入场资金
      async ticketsClick8 () {
          let value = this.ticketsInput8;
          //value = this.$util.mul10e18(value);
          value = value * 1;
          let res = await this.$contract.setMaxVendAmountPerDay(value);
          console.log('门票合约每日最大入场资金-返回值', res);
          let that =this;
          setTimeout(async () => {
              //获取当前管理者数量
              //await that.getMaxVendAmountPerDay()
              this.maxVendAmountPerDay = await this.$contract.getMaxVendAmountPerDay();
              this.maxVendAmountPerDay = this.$util.hexToDec(this.maxVendAmountPerDay._hex);
              console.log("this.maxVendAmountPerDay",this.maxVendAmountPerDay);
          }, 2000)
      },

      //获取门票合约每日最大入场资金
      async getMaxVendAmountPerDay() {
          this.maxVendAmountPerDay = await this.$contract.getMaxVendAmountPerDay();
          this.maxVendAmountPerDay = this.$util.hexToDec(this.maxVendAmountPerDay._hex);
          console.log("this.maxVendAmountPerDay",this.maxVendAmountPerDay);
      },

    /* 管理员相关方法 */
    // 更换主合约owner
    async ownerClick1 () {
      let address = this.ownerInput1;
      let res = await this.$contract.changeOwnerMaster(address);
      console.log('更换主合约owner-返回值', this.ownerInput1, res)
    },

    // 更换门票合约owner
    async ownerClick2 () {
      let address = this.ownerInput2;
      let res = await this.$contract.changeOwnerTickets(address);
      console.log('更换门票合约owner-返回值', this.ownerInput2, res)
    },

    // 设置主合约的一个管理者
    async ownerClick3 () {
      let address1 = this.ownerInput31;
      // let address2 = this.ownerInput32;
      // let address3 = this.ownerInput33;
      console.log(address1);
      let res = await this.$contract.setMasterManagers(address1);
      console.log('设置主合约的一个管理者-返回值', res);
      setTimeout(async () => {
        //获取当前管理者数量
        let res9 = await this.$contract.masterMangersLen();
        this.masterManagerLen = this.$util.hexToDec(res9._hex);
        console.log('this.masterManagerLen:', this.masterManagerLen)
      }, 2000)
    },

    // 设置门票合约的一个管理者
    async ownerClick4 () {
      let address1 = this.ownerInput41;
      // let address2 = this.ownerInput42;
      // let address3 = this.ownerInput43;
      console.log(address1);
      let res = await this.$contract.setTicketsManagers(address1);
      console.log('设置门票合约的一个管理者-返回值', res);
      setTimeout(async () => {
        //获取当前管理者数量
        let res10 = await this.$contract.ticketMangersLen();
        this.ticketManagerLen = this.$util.hexToDec(res10._hex);
        console.log('this.ticketManagerLen:', this.ticketManagerLen)
      }, 2000)
    }
  }
}
</script>

<style>
.owner-title {
  font-weight: bold;
  margin-bottom: 20px;
}
.owner-head {
  margin-bottom: 10px;
}
.owner-input {
  margin-bottom: 5px;
}
.owner-tips {
  margin-top: 5px;
  color: darkgrey;
}
.owner-btn {
  margin: 10px 0 10px 0;
}
.owner-hr {
  background: #ffb400;
  width: 100%;
  height: 1px;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 30px;
}

@media only screen and (min-width: 415px) {
  .owner-w {
    margin: 0 auto;
    width: 50%;
    background: #ffffff;
    border: 1px solid #f5f5f5;
    padding: 15px 15px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
  }
  .owner-tips {
    font-size: 15px;
  }
}

/* 宽度尺寸小于414时 */
@media only screen and (max-width: 414px) {
  .owner-w {
    background: #ffffff;
    border: 1px solid #f5f5f5;
    padding: 15px 15px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
  }
  .owner-tips {
    font-size: 13px;
  }
}
</style>



